/** @jsx jsx */
import * as React from "react"
import { Global } from "@emotion/react"
import { Box, Container, jsx } from "theme-ui"
import VerticalScrollWrap from "./verticalScrollWrap"
import Seo from "./seo"
import Header from "./header/header"
import Footer from "./footer"
import CodeStyles from "../styles/code"
import Prism from "prism-react-renderer/prism";
(typeof global !== "undefined" ? global : window).Prism = Prism
require("prismjs/components/prism-haskell")
require("prismjs/components/prism-clojure")
require("prismjs/components/prism-java")
require("prismjs/components/prism-rescript")

type LayoutProps = { children: React.ReactNode; className?: string, selectedCategory?: string }

const Layout = ({ children, className, selectedCategory }: LayoutProps) => {
  const [scrollFlag, setScrollFlag] = React.useState(false);

  return (
    <VerticalScrollWrap setScrollFlag={setScrollFlag}>
      <React.Fragment>
        <Seo />
        <Container>
          <Header selectedCategory={selectedCategory} scrollFlag={scrollFlag} />
          <Box className={'pt-[64px] '} sx={{ ...CodeStyles }}>
            {children}
          </Box>
          <Footer />
        </Container>
      </React.Fragment >
    </VerticalScrollWrap>
  )
}

export default Layout
