
import React, { ReactNode } from 'react';
import { useState } from "react"

interface Type {
    children: ReactNode;
    setScrollFlag: (value: boolean) => void;
}



const VerticalScrollWrap = ({ children, setScrollFlag }: Type) => {
    const [lastScrollY, setLastScrollY] = useState<Number>(0);
    const [timerScrollY, setTimerScrollY] = useState<Number>(0);

    if (typeof window !== 'undefined') {
        React.useEffect(() => {
            window.addEventListener('scroll', () => {
                const { scrollY } = window;
                if (scrollY > 0) {
                    setScrollFlag(true)

                } else {
                    setScrollFlag(false)

                }

            });
        }, [scrollY]);
    }







    return <>{children}</>;
};

export default VerticalScrollWrap;